import React, { useState, useEffect } from 'react';
import { Container, Row, Col, FormGroup, InputGroup, Button} from 'react-bootstrap';
import { Tabs, Tab} from 'react-bootstrap';
import axios from 'axios';
import BigNumber from "bignumber.js";
import { object } from 'prop-types';
import { convertSlugToUrl } from '../../resources/utilities';

function ParagonCalculatorComponent() {

    const [paragonCapArray, setParagonCapArray] = useState([]);
    const [combine2ParagonSeasonalParagon, setCombine2ParagonSeasonalParagon] = useState(0);
    const [combine2ParagonNonSeasonalParagon, setCombine2ParagonNonSeasonalParagon] = useState(0);
    const [combine2ParagonResult, setCombine2ParagonResult] = useState(0);

    const resetCombine2ParagonTab = () => {
        setCombine2ParagonSeasonalParagon(0);
        setCombine2ParagonNonSeasonalParagon(0);
        setCombine2ParagonResult(0);
    }

    const calculateCombine2Paragon = () => {
        let tempSeasonalParagonTotalExp = new BigNumber(findTotalExpOnLvl(parseFloat(combine2ParagonSeasonalParagon), paragonCapArray));
        let tempNonSeasonalParagonTotalExp = new BigNumber(findTotalExpOnLvl(parseFloat(combine2ParagonNonSeasonalParagon), paragonCapArray));
        let tempMergeParagonTotalExp = new BigNumber(0).plus(tempSeasonalParagonTotalExp).plus(tempNonSeasonalParagonTotalExp);
        setCombine2ParagonResult(findNearestLvlOnExp(tempMergeParagonTotalExp.toNumber(), paragonCapArray));
    }

    const findTotalExpOnLvl = (lvl, paragonCapArray) => {

        let start = 0;
        let end = paragonCapArray.length - 1;
        
        while (start <= end) {
            let middle = Math.floor((start + end) / 2);
            if (paragonCapArray[middle].paragonLevel === lvl) {
                return paragonCapArray[middle].totalExp;
            } else if (paragonCapArray[middle].paragonLevel < lvl) {
                start = middle + 1;
            } else {
                end = middle - 1;
            }
        }

        return - 1;
    } 

    const findNearestLvlOnExp = (exp, paragonCapArray) => {
        let start = 0;
        let end = paragonCapArray.length - 1;

        while (start <= end) {
            let middle = Math.floor((start + end) / 2);
            if (exp < paragonCapArray[middle].totalExp) {
                end = middle - 1;
            } else if (exp > paragonCapArray[middle].totalExp) {
                start = middle + 1;
            } else {
                return paragonCapArray[middle].paragonLevel;
            }
        }

        return (paragonCapArray[start].totalExp - exp) < (exp - paragonCapArray[end]) 
                    ? paragonCapArray[start].paragonLevel : paragonCapArray[end].paragonLevel;

    }

    useEffect(() => {

        const fetchData = async () =>{
            try {
                const {data: response} = await axios.get('./assets/data/paragonCap.json');
                setParagonCapArray(...paragonCapArray, response);
            } catch (error) {
                console.error(error.message);
            }
        }

        fetchData();

    }, []);

    return (
        <Container fluid="lg">
            {/* <Tabs className="mb-3" defaultActiveKey="combine2Paragon" transition={false}> */}
                {/* <Tab eventKey="combine2Paragon" title="Combine 2 Paragon"> */}
                    <Row>
                        <Col md> 
                            <FormGroup className="w-50">
                                <p className="fw-bold" >
                                    Seasonal Paragon
                                </p>
                                <InputGroup className="mb-1" >
                                    <input className="form-control" type="number" value={combine2ParagonSeasonalParagon}
                                        onChange={e => setCombine2ParagonSeasonalParagon(e.target.value)} />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup className="w-50" style={{marginTop: "10px"}}>
                                <p className="fw-bold" >
                                    Non Seasonal Paragon
                                </p>
                                <InputGroup className="mb-1" >
                                    <input className="form-control" type="number" value={combine2ParagonNonSeasonalParagon}
                                        onChange={e => setCombine2ParagonNonSeasonalParagon(e.target.value)} />
                                </InputGroup>
                            </FormGroup>
                            <FormGroup className="w-50" style={{marginTop: "10px"}}>
                                <p className="fw-bold" >
                                    Result
                                </p>
                                <InputGroup className="mb-1" >
                                    <input className="form-control" type="number" value={combine2ParagonResult} disabled readOnly/>
                                </InputGroup>
                            </FormGroup>
                            <div style={{marginTop: "20px"}}>
                                <Button style={{float: "right", backgroundColor: "red", borderColor: "red"}} onClick={() => resetCombine2ParagonTab()}>Reset</Button>
                                <Button style={{float: "right", backgroundColor: "green", borderColor: "green", marginRight: "10px"}}
                                        onClick={() => calculateCombine2Paragon()}>
                                    Calculate
                                </Button>
                            </div>
                        </Col>
                    </Row>
                {/* </Tab> */}
            {/* </Tabs> */}
        </Container>
    );
}

export default ParagonCalculatorComponent;
