import React, {useEffect, useState} from 'react';

import { Container, Row, Col } from 'react-bootstrap';
import { any, func } from 'prop-types';
import classesSkillData from '../../../resources/classesSkillData';

import BarbarianIcon from '../../../assets/image/classes/barbarian_male.png';
import CrusaderIcon from '../../../assets/image/classes/crusader_male.png';
import DemonHunterIcon from '../../../assets/image/classes/demonhunter_female.png';
import MonkIcon from '../../../assets/image/classes/monk_male.png';
import NecromancerIcon from '../../../assets/image/classes/necro_male.png';
import WitchDoctorIcon from '../../../assets/image/classes/witchdoctor_male.png';
import WizardIcon from '../../../assets/image/classes/wizard_female.png'

import TemplarIcon from '../../../assets/image/classes/templar.png';
import EnchantressIcon from '../../../assets/image/classes/enchantress.png';
import ScoundrelIcon from '../../../assets/image/classes/scoundrel.png';    

function ClassesFrameComponent({ children, parentCallback}) {

    const [selectedClass, setSelectedClass] = useState("");

    const classesIconImgPathMap = {
        "d3_barbarian": BarbarianIcon,
        "d3_crusader": CrusaderIcon,
        "d3_demonhunter": DemonHunterIcon,
        "d3_monk": MonkIcon,
        "d3_necromancer": NecromancerIcon,
        "d3_witchdoctor": WitchDoctorIcon,
        "d3_wizard": WizardIcon,
        "d3_templar": TemplarIcon,
        "d3_enchantress": EnchantressIcon,
        "d3_scoundrel": ScoundrelIcon
    }

    const getClassIconImage = (id) => {
        let obj = null;
        Object.entries(classesIconImgPathMap).forEach(([key, value]) => {
            if (key == id) {
                obj = value;
                return obj;
            }
        });
        return obj
    }

    const setOnSelectedClass = (selectedClass) => {   
        setSelectedClass(selectedClass);     
        parentCallback(selectedClass);
    }

    useEffect(() => {
        setOnSelectedClass("d3_barbarian");
    }, [])

    return (
        <Row className="classesFrame-body h-100 w-100 border rounded-3">
            <Col md={5} style={{marginTop: '20px'}}>   
                <ul style={{listStyleType: 'none', width: '90%'}}>
                    {
                        classesSkillData.map((obj, index) => {
                            return(
                                <li className={"hero-frame " + ((selectedClass == obj.class) ? "clickedHeroFrame" : "")   } key={obj.class} onClick={() => setOnSelectedClass(obj.class)}>
                                    <div style={{display: 'flex'}}>
                                        <img className="hero-img-frame" src={getClassIconImage(obj.class)} style={{float: 'left', alignSelf: 'center'}}/>
                                        <span style={{float: 'left', alignSelf: 'center'}}>{obj.engName}</span>
                                    </div>        
                                </li>
                            );
                        })
                    }
                </ul>
            </Col>
            <Col md={7} style={{marginTop: '20px'}}>
                {children}
            </Col>
        </Row>
    );
}

ClassesFrameComponent.defaultProps = {};

ClassesFrameComponent.propTypes = {
    children: any,
    parentCallback: func
};

export default ClassesFrameComponent;