import { element } from 'prop-types';
import React, { useState } from 'react';
import { Container, Row, Col, Button, InputGroup, FormGroup, FormCheck} from 'react-bootstrap';

function CDRCalculatorComponent() {

    const [totalCdr, setTotalCdr] = useState((0).toFixed(2));
    const [headCdr, setHeadCdr] = useState("");
    const [shoulderCdr, setShoulderCdr] = useState("");
    const [handsCdr, setHandsCdr] = useState("");
    const [waistCdr, setWaistCdr] = useState("");
    const [amuletCdr, setAmuletCdr] = useState("");
    const [ring1Cdr, setRing1Cdr] = useState("");
    const [ring2Cdr, setRing2Cdr] = useState("");
    const [weapon1Cdr, setWeapon1Cdr] = useState("");
    const [weapon2Cdr, setWeapon2Cdr] = useState("");
    const [paragonCdr, setParagonCdr] = useState("");

    const [timeBetweenSkillUse, setTimeBetweenSkillUse] = useState((10).toFixed(2));
    const [skillActiveDuration, setSkillActiveDuration] = useState("");
    const [skillCooldown, setSkillCooldown] = useState("");
    
    const [additionalCDRs, setAdditionalCDRs] = useState([]);

    const calculate = () => {

        let tempTotalCdr = 0, result = 0, timeBetweenSkill = 0, tempAdditionalCDRs = 1;

        if (additionalCDRs != null && additionalCDRs.length > 0) {
            for (let i = 0; i < additionalCDRs.length; i++) {
                if (tempAdditionalCDRs == 1) {
                    tempAdditionalCDRs = ( 1 - (additionalCDRs[i].value/100));
                } else {
                    tempAdditionalCDRs = tempAdditionalCDRs * ( 1 - (additionalCDRs[i].value/100));
                }
            }
            if (tempAdditionalCDRs != 1) {
                tempAdditionalCDRs = tempAdditionalCDRs.toFixed(2);
            }
        }

        tempTotalCdr =(( 1 -  
                        ( 1 - (headCdr/100) ) * ( 1 - (shoulderCdr/100) ) * ( 1 - (handsCdr/100) ) *  ( 1 - (waistCdr/100) ) * 
                        ( 1 - (amuletCdr/100) ) * ( 1 - (ring1Cdr/100) ) * ( 1 - (ring2Cdr/100) ) * ( 1 - (weapon1Cdr/100) ) *
                        ( 1 - (weapon2Cdr/100) ) * ( 1 - (paragonCdr/100) ) * tempAdditionalCDRs) * 100).toFixed(2);
        timeBetweenSkill = (skillCooldown * ( 1 - (tempTotalCdr) / 100 )).toFixed(2);

        if (document.getElementById("cdBeginAfterSkillFinish").checked) {
            console.log(skillActiveDuration);
            result = new Number(parseFloat(timeBetweenSkill.toString()).toFixed(2)) + new Number(parseFloat(skillActiveDuration.toString()).toFixed(2));
        } else {
            result =  timeBetweenSkill;
        }

        setTimeBetweenSkillUse(result);
        setTotalCdr(tempTotalCdr);

    }

    const clearFields = () => {
        setTotalCdr((0).toFixed(2));
        setTimeBetweenSkillUse((10).toFixed(2));

        setHeadCdr("");
        setShoulderCdr("");
        setHandsCdr("");
        setWaistCdr("");
        setAmuletCdr("");
        setRing1Cdr("");
        setRing2Cdr("");
        setWeapon1Cdr("");
        setWeapon2Cdr("");
        setParagonCdr("");

        setSkillActiveDuration("");
        setSkillCooldown("");
        document.getElementById("cdBeginAfterSkillFinish").checked = false;
        if (additionalCDRs != null && additionalCDRs.length > 0) {
            for (let i = 0; i < additionalCDRs.length; i++) {
                document.getElementById("additionalCDR" + i).value = "0";
            }
        }
    }

    const handleAdditionalCDRChange = (index, event) => {
        let tempAdditionalCDRs = [...additionalCDRs];
        tempAdditionalCDRs[index][event.target.name] =  parseFloat(event.target.value);
        setAdditionalCDRs(tempAdditionalCDRs);
    }

    let addAdditionalCDRFields = () => {
        setAdditionalCDRs([...additionalCDRs, {value: 0}]);
    }

    let removeAdditionalCDRFields = (index) => {
        let tempAdditionalCDRs = [...additionalCDRs];
        tempAdditionalCDRs.splice(index, 1);
        setAdditionalCDRs(tempAdditionalCDRs);
    }

    return (
        <Container fluid="lg">
            {/* Result */}
            <Row className="border border-dark rounded">
                {/* Total CDR Result */}
                <Col lg={4} style={{ marginTop: "10px" }}>
                    <p className="fw-bold">Total Cooldown Reduction</p>
                    <InputGroup className="mb-2">
                        <input id="totalCdrResult" className="form-control" value={totalCdr} type="number" disabled readOnly/>
                        <span className="input-group-text">%</span>
                    </InputGroup>
                </Col>
                <Col lg={4} style={{ marginTop: "10px" }}>
                    <p className="fw-bold" >Time Between Skill Use</p>
                    <InputGroup className="mb-2">
                        <input id="timeBetweenSkillUseResult" className="form-control" value={timeBetweenSkillUse} type="number" disabled readOnly/>
                        <span className="input-group-text">%</span>
                    </InputGroup>
                </Col>
                <Col lg={4} id="calCdrMainBtnGrp">
                    <Button style={{float: "right", backgroundColor: "red", borderColor: "red"}} onClick={clearFields}>Clear Fields</Button>
                    <Button style={{float: "right", marginRight: "10px", backgroundColor: "green", borderColor: "green"}} onClick={calculate}>Calculate</Button>
                </Col>                        
            </Row>

            {/* Calculators */}
            <Row>
                <Col sm={8} className="border border-dark rounded" style={{ marginTop: "10px" }}>
                    <p style={{ marginTop: "10px" }}>
                        Max values are only placeholders - feel free to put in whatever values you'd
                        like.
                    </p>

                    <Row>
                        {/* Fixed Column */}
                        <Col lg={6} >
                            <FormGroup>
                                <p className="fw-bold" >
                                    Head
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="headCdr" className="form-control" type="number" placeholder="Max: 25%" value={headCdr}
                                        onChange={e => setHeadCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Shoulder
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="shoulderCdr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setShoulderCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Hands
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="handsCdr" className="form-control" type="number" placeholder="Max: 8%" 
                                        onChange={e => setHandsCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Waist
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="waistCdr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setWaistCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Amulet
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="amuletCdr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setAmuletCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Ring 1
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="ring1Cdr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setRing1Cdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Ring 2
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="ring2Cdr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setRing2Cdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <div style={{height: '10px'}}></div>
                        </Col>

                        {/* Fixed Column */}
                        <Col lg={6}>
                            <FormGroup>
                                <p className="fw-bold">
                                    Weapon 1
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="weapon1Cdr" className="form-control" type="number" placeholder="Max: 10%"
                                        onChange={e => setWeapon1Cdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold">
                                    Weapon 2
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="weapon2Cdr" className="form-control" type="number" placeholder="Max: 10%"
                                        onChange={e => setWeapon2Cdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold">
                                    Paragon
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="paragonCdr" className="form-control" type="number" placeholder="Max: 10%"
                                        onChange={e => setParagonCdr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            {additionalCDRs.map((element, index) => (
                                <FormGroup key={index}>
                                    <p className="fw-bold">
                                        Additional CDR
                                    </p>
                                    <InputGroup className='mb-3'>
                                        <input className="form-control" id={"additionalCDR" + index}  name="value" type="number" placeholder="0" onChange={e => handleAdditionalCDRChange(index, e)}/>
                                        <span className="input-group-text">%</span>
                                        <Button style={{marginLeft: '10px', backgroundColor: 'gray', borderColor: 'gray'}} onClick={() => removeAdditionalCDRFields(index)}>Remove</Button>
                                    </InputGroup>
                                </FormGroup>
                            ))}
                            <FormGroup>
                                <Button onClick={() => addAdditionalCDRFields()}>Add fields</Button>
                            </FormGroup>
                            <div style={{height: '10px'}}></div>
                        </Col>
                    </Row>
                </Col>
                <Col sm={1}/>
                <Col sm={3} className='border border-dark rounded' style={{ marginTop: "10px" }}>

                    <p style={{ marginTop: "10px" }}>
                        Skill Information
                    </p>

                    <Row>
                        <Col sm={12}>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Skill Active Duration
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="skillActiveDuration" className="form-control" type="number" value={skillActiveDuration}
                                        onChange={e => setSkillActiveDuration(e.target.value)} />
                                    <span className="input-group-text">sec</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Skill Cooldown
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="skillCooldown" className="form-control" type="number" value={skillCooldown}
                                        onChange={e => setSkillCooldown(e.target.value)} />
                                    <span className="input-group-text">sec</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Cooldown Begins After Skill Finishes
                                </p>
                                <FormCheck>
                                    <input className="form-check-input" type="checkbox"  id="cdBeginAfterSkillFinish" />
                                    <label className="form-check-label" htmlFor="cdBeginAfterSkillFinish">
                                        Yes
                                    </label>
                                </FormCheck>
                            </FormGroup>
                        </Col>
                    </Row>
                </Col>
            </Row>
        </Container>
    );
}

export default CDRCalculatorComponent;
