import React, { Suspense, lazy } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import loadable from '@loadable/component'
import menumap from '../resources/menumap';
import LoadingComponent from '../components/loading';
import DashboardComponent from './dashboard/DashboardComponent';

function Router() {

    // const DashboardComponent = loadable(() =>
    //     import(/* webpackChunkName: "DashboardComponent" */ './dashboard/DashboardComponent')
    // );

    const CDRCalculatorComponent = loadable(() =>
        import(/* webpackChunkName: "CDRCalculatorComponent" */ './calculator/CDRCalculatorComponent')
    );

    const RCRCalculatorComponent = loadable(() =>
        import(/* webpackChunkName: "RCRCalculatorComponent" */ './calculator/RCRCalculatorComponent')
    );

    const GemCalculatorComponent = loadable(() =>
        import(/* webpackChunkName: "GemCalculatorComponent" */ './calculator/GemCalculatorComponent')
    );

    const ParagonCalculatorComponent = loadable(() =>
        import(/* webpackChunkName: "ParagonCalculatorComponent" */ './calculator/ParagonCalculatorComponent')
    );

    const COESimulatorComponent = loadable(() =>
        import(/* webpackChunkName: "COESimulatorComponent" */ './simulator/COESimulatorComponent')
    );

    const SkillSimulatorComponent = loadable(() =>
        import(/* webpackChunkName: "SkillSimulatorComponent" */ './simulator/SkillSimulatorComponent')
    );

    const KadalaSimulatorComponent = loadable(() =>
        import(/* webpackChunkName: "KadalaSimulatorComponent" */ './simulator/KadalaSimulatorComponent')
    );

    const BluePostComponent = loadable(() =>
        import(/* webpackChunkName: "BluePostComponent" */ './bluepost/BluePostComponent')
    );

    const NotFountComponent = loadable(() =>
        import(/* webpackChunkName: "NotFountComponent" */ './404/NotFountComponent')
    );

    return (
        <Suspense fallback={<LoadingComponent loading />}>
            <Switch>
                <Route exact path="/" component={DashboardComponent}/>
                <Route path={menumap.dashboard} component={DashboardComponent}/>
                <Route path={menumap.calculator_cdr} component={CDRCalculatorComponent} />
                <Route path={menumap.calculator_rcr} component={RCRCalculatorComponent} />
                <Route path={menumap.calculator_gems} component={GemCalculatorComponent} />
                <Route path={menumap.calculator_paragon} component={ParagonCalculatorComponent} />
                <Route path={menumap.simulator_coe} component={COESimulatorComponent} />
                <Route path={menumap.simulator_skill} component={SkillSimulatorComponent} />
                <Route path={menumap.simulator_kadala} component={KadalaSimulatorComponent} />
                <Route path={menumap.bluepost} component={BluePostComponent} />
                <Route path="*" component={NotFountComponent}/>
            </Switch>
        </Suspense>
    );
}

export default Router;
