import React, { useState } from 'react';
import { Container, Row, Col, Button, InputGroup, FormGroup} from 'react-bootstrap';

function RCRCalculatorComponent() {

    const [totalRcr, setTotalRcr] = useState((0).toFixed(2));
    const [headRcr, setHeadRcr] = useState("");
    const [shoulderRcr, setShoulderRcr] = useState("");
    const [handsRcr, setHandsRcr] = useState("");
    const [amuletRcr, setAmuletRcr] = useState("");
    const [ring1Rcr, setRing1Rcr] = useState("");
    const [ring2Rcr, setRing2Rcr] = useState("");
    const [weapon1Rcr, setWeapon1Rcr] = useState("");
    const [weapon2Rcr, setWeapon2Rcr] = useState("");
    const [paragonRcr, setParagonRcr] = useState("");

    const [additionalRCRs, setAdditionalRCRs] = useState([]);

    const calculate = () => {

        let tempTotalRcr = 0, tempAdditionalRCRs = 1;

        if (additionalRCRs != null && additionalRCRs.length > 0) {
            for (let i = 0; i < additionalRCRs.length; i++) {
                if (tempAdditionalRCRs == 1) {
                    tempAdditionalRCRs = ( 1 - (additionalRCRs[i].value/100));
                } else {
                    tempAdditionalRCRs = tempAdditionalRCRs * ( 1 - (additionalRCRs[i].value/100));
                }
            }
            if (tempAdditionalRCRs != 1) {
                tempAdditionalRCRs = tempAdditionalRCRs.toFixed(2);
            }
        }

        tempTotalRcr =(( 1 -  
                        ( 1 - (headRcr/100) ) * ( 1 - (shoulderRcr/100) ) * ( 1 - (handsRcr/100) ) * ( 1 - (amuletRcr/100) ) * 
                        ( 1 - (ring1Rcr/100) ) * ( 1 - (ring2Rcr/100) ) * ( 1 - (weapon1Rcr/100) ) * ( 1 - (weapon2Rcr/100) ) * 
                        ( 1 - (paragonRcr/100) ) * tempAdditionalRCRs) * 100).toFixed(2);
        
        setTotalRcr(tempTotalRcr);

    }

    const clearFields = () => {
        setTotalRcr((0).toFixed(2));

        setHeadRcr("");
        setShoulderRcr("");
        setHandsRcr("");
        setAmuletRcr("");
        setRing1Rcr("");
        setRing2Rcr("");
        setWeapon1Rcr("");
        setWeapon2Rcr("");
        setParagonRcr("");
        if (additionalRCRs != null && additionalRCRs.length > 0) {
            for (let i = 0; i < additionalRCRs.length; i++) {
                document.getElementById("additionalRCR" + i).value = "0";
            }
        }

    }

    const handleAdditionalRCRChange = (index, event) => {
        let tempAdditionalRCRs = [...additionalRCRs];
        tempAdditionalRCRs[index][event.target.name] =  parseFloat(event.target.value);
        setAdditionalRCRs(tempAdditionalRCRs);
    }

    let addAdditionalRCRFields = () => {
        setAdditionalRCRs([...additionalRCRs, {value: 0}]);
    }

    let removeAdditionalRCRFields = (index) => {
        let tempAdditionalRCRs = [...additionalRCRs];
        tempAdditionalRCRs.splice(index, 1);
        setAdditionalRCRs(tempAdditionalRCRs);
    }

    return (
        <Container fluid="lg">

            <Row className="border border-dark rounded">
                {/* Total RCR Result */}
                <Col lg={4} style={{ marginTop: "10px" }}>
                    <p className="fw-bold">Total Resources Cost Reduction</p>
                    <InputGroup className="mb-2">
                        <input id="totalRcrResult" className="form-control" value={totalRcr} type="number" disabled readOnly/>
                        <span className="input-group-text">%</span>
                    </InputGroup> 
                </Col>
                <Col lg={4}/>
                <Col lg={4} id="calRcrMainBtnGrp">
                    <Button style={{float: "right", backgroundColor: "red", borderColor: "red"}} onClick={clearFields}>Clear Fields</Button>
                    <Button style={{float: "right", marginRight: "10px", backgroundColor: "green", borderColor: "green"}} onClick={calculate}>Calculate</Button>
                </Col>
            </Row>

            {/* Calculators */}
            
                <Row sm={12} className="border border-dark rounded" style={{ marginTop: "10px" }}>
                    <p style={{ marginTop: "10px" }}>
                        Max values are only placeholders - feel free to put in whatever values you'd
                        like.
                    </p>

                    <Row>
                        {/* Fixed Column */}
                        <Col lg={6} >
                            <FormGroup>
                                <p className="fw-bold" >
                                    Head
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="headRcr" className="form-control" type="number" placeholder='Max: 25%' value={headRcr}
                                        onChange={e => setHeadRcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Shoulder
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="shoulderRcr" className="form-control" type="number" placeholder="Max: 8%" 
                                        onChange={e => setShoulderRcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Hands
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="handsRcr" className="form-control" type="number" placeholder="Max: 8%" 
                                        onChange={e => setHandsRcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Amulet
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="amuletRcr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setAmuletRcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Ring 1
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="ring1Rcr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setRing1Rcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold" >
                                    Ring 2
                                </p>
                                <InputGroup className="mb-2" >
                                    <input id="ring2Rcr" className="form-control" type="number" placeholder="Max: 8%"
                                        onChange={e => setRing2Rcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <div style={{height: "10px"}}></div>
                        </Col>

                        {/* Fixed Column */}
                        <Col lg={6}>
                            <FormGroup>
                                <p className="fw-bold">
                                    Weapon 1
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="weapon1Rcr" className="form-control" type="number" placeholder="Max: 10%"
                                        onChange={e => setWeapon1Rcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold">
                                    Weapon 2
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="weapon2Rcr" className="form-control" type="number" placeholder="Max: 10%" 
                                        onChange={e => setWeapon2Rcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            <FormGroup>
                                <p className="fw-bold">
                                    Paragon
                                </p>
                                <InputGroup className="mb-2">
                                    <input id="paragonRcr" className="form-control" type="number" placeholder="Max: 10%" 
                                        onChange={e => setParagonRcr(e.target.value)} />
                                    <span className="input-group-text">%</span>
                                </InputGroup>
                            </FormGroup>
                            {additionalRCRs.map((element, index) => (
                                <FormGroup key={index}>
                                    <p className="fw-bold">
                                        Additional RCR
                                    </p>
                                    <InputGroup className="mb-3">
                                        <input className="form-control" id={"additionalRCR" + index}  name="value" type="number" placeholder="0" onChange={e => handleAdditionalRCRChange(index, e)}/>
                                        <span className="input-group-text">%</span>
                                        <Button style={{marginLeft: "10px", backgroundColor: "gray", borderColor: "gray"}} onClick={() => removeAdditionalRCRFields(index)}>Remove</Button>
                                    </InputGroup>
                                </FormGroup>
                            ))}
                            <FormGroup>
                                <Button onClick={() => addAdditionalRCRFields()}>Add fields</Button>
                            </FormGroup>
                            <div style={{height: "10px"}}></div>
                        </Col>
                    </Row>
                </Row>
            
        </Container>
    );
}

export default RCRCalculatorComponent;
